export class Solution {
    private hostDiv : HTMLDivElement;
    constructor(id: string, param: any) {
        this.hostDiv = document.getElementById(id) as HTMLDivElement;
        window.open(param.url, '_blank');
    }
    public destroy() {
        while (this.hostDiv.lastElementChild) {
            this.hostDiv.removeChild(this.hostDiv.lastElementChild);
        }
    }
}

